import React, { useEffect, useRef, useState } from "react";
import { Form, Select } from "antd"
import _ from "lodash"
import QrScanner from "qr-scanner";

import "./QrStyles.css";
const { Option } = Select

const QrReader = () => {
  const scanner = useRef();
  const videoEl = useRef(null)
  const [camera, setCamera] = useState("environment")
  const cameras = useRef([])

  const getCameraList = async () => {
    let cameraList = await QrScanner.listCameras(true)
    if (!_.isEmpty(cameraList)) {
      cameras.current = cameraList;
    }
  }

  useEffect(() => {
    getCameraList();
    scanner.current = new QrScanner(videoEl.current, (result) => { if (!_.isEmpty(result.data)) { alert(result.data) } }, {
      highlightScanRegion: true,
      // highlightCodeOutline: true,
    });
    scanner.current.start();
  }, [])

  useEffect(() => {
    scanner.current.setCamera(camera)
  }, [camera])

  return (
    <div className="qr-reader">
      <Form.Item label="Camera: ">
        <Select value={camera} onChange={(value) => setCamera(value)}>
          <Option value="environment" selected>Environment Facing (default)</Option>
          <Option value="user">User Facing</Option>
          {_.map(cameras.current, ({ id, label }) => (
            <Option key={id} value={id}>{label}</Option>
          ))}
        </Select>
      </Form.Item>
      <video ref={videoEl}></video>
    </div>
  );
};

export default QrReader;
